$border-color: #555;
$border: 1px solid $border-color;
$no-border: 0px solid white;
$padding-width: 5px;

$left-column-width: 500px;

$top-panel-height: 35px;
$top-panel-content-margin: 5px;
$top-panel-button-height: $top-panel-height - 2 * $top-panel-content-margin;

$font-color: #233527;
$smallFont-size: 14px;

@mixin vertical-flex-panel() {
    display: flex;
    border: 1px solid #afafaf;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    height: 100%; }

.app-panel {
    height: 100%;
    div {
        box-sizing: border-box; } }

.top-panel {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    background: linear-gradient(to top, #f5feea, #ace6ba);
    height: $top-panel-height;
    span {
        margin: 5px;
        font-size: $smallFont-size;
        color: $font-color; }
    > div {
        display: inline-flex;
        justify-content: center;
        margin: 5px; }
    > div:last-child {
        right: 0px; } }

.main-panel {
    position: fixed;
    display: flex;
    padding-top: $top-panel-height;
    width: 100%;
    height: 100vh;
    > div {
        display: inline-block; }
    form {
        padding: 10px;
        background: linear-gradient(to bottom, #f5feea, #ace6ba);
        border-top: 1px solid #afafaf; } }

.user-chats-panel {
    width: $left-column-width;
    background-color: #ddebe8;
    > div {
        @include vertical-flex-panel(); } }

.current-chat-panel {
    width: 100%;
    background: url(../resources/sky.jpg) center center no-repeat fixed #ffffff;
    > div {
        @include vertical-flex-panel(); } }
